.App {
  text-align: center;
  
}

.displayComp{
  display: none;
}

.header{
  font-size: 45px;
  font-weight: 800;
  font-family: NeueMachina-Ultrabold, serif;
}
.rider{
  font-weight: 700;
  font-size: 20px;
  font-family: NeueMachina-Regular,  serif;
}
.setDate{
  position: relative;
  margin-top: 20px;
  width: 150px;
  padding: 10px;
  border: 1px solid black;
  outline: none;
  border-radius: 6px;
  background-color: #ffffffe3;
  font-family: serif;
  font-size: 18px;
  color: black;
}
::-webkit-calendar-picker-indicator{
   cursor: pointer;
   background-color: white;
}
.btns{
  display: flex;
  justify-content: space-between;
  margin: 3rem 15rem;
}
.reset{
  /* margin-top: 4rem; */
  /* margin-right: 5rem; */
  background-color: #FF6767;
  border: 1px solid white;
  border-radius: 1rem;
  box-sizing: border-box;
  color: white;
  cursor: pointer;
  width: 120px;
  font-family: Inter,sans-serif;
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.5;
  padding: .5rem 1rem;
  text-align: center;
}
.setbutton{
 margin-top: 0;
 margin-left: 5rem;
    background-color: #3DD97C;
    border: 1px solid white;
    border-radius: 1rem;
    box-sizing: border-box;
    color: white;
    cursor: pointer;
    display: inline-block;
    font-family: Inter,sans-serif;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.5;
   
    padding: .5rem 1rem;
    text-align: center;
    text-transform: none;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: middle;
  
}
.countdown{
  margin: 5rem 0 10% 0; 
  font-weight: 700;
  font-size: 30px;
  font-family: serif;
}
footer{
  background-color: darkgray;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

@media only screen and (max-device-width: 480px) {
  .App{
    padding: 0 20px;
  }
  .rider{
    font-weight: 700;
    font-size: 18px;
    font-family: NeueMachina-Regular,  serif;
  }
  .btns{
    display: flex;
    margin: 3rem 1rem;
  }
   
  .resetbtn{
    margin: 0;
  }
  .reset{
  
    /* position: absolute;
    top: 40%;
    right: 60%; */
  }
  .setbtn{
    margin: 0 0 0 0;
  }
  
  .countdown{
    margin-top: 4rem;
  }
}